.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.slider:before {
  content: "";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input:checked + .slider {
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.scroll-up-btn {
  z-index: 99;
  opacity: 0;
  cursor: pointer;
  background-color: #4f2ee8;
  border: none;
  border-radius: 50%;
  padding: 15px;
  line-height: 0;
  transition: background-color .5s cubic-bezier(.65, .05, .36, 1), box-shadow .5s cubic-bezier(.65, .05, .36, 1), opacity .5s cubic-bezier(.65, .05, .36, 1);
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 0 0 5px rgba(79, 46, 232, .3);
}

.scroll-arr {
  fill: #fff;
}

.scroll-up-btn:hover {
  background-color: #fff;
  box-shadow: 0 0 0 3px rgba(79, 46, 232, .3);
}

.scroll-up-btn:hover .scroll-arr {
  fill: #4f2ee8;
}

